/* BubbleButton */
.BubbleButton
{
  --c: var(--Orange);
  --c: var(--SkyBlue);
  color: var(--c);
  font-size: 1.5rem;
  border: 0.3em solid var(--c);
  border-radius: 0.5em;
  /* width: 12em;
  height: 3em; */
  text-transform: uppercase;
  font-weight: bold;
  font-family: sans-serif;
  letter-spacing: 0.1em;
  text-align: center;
  line-height: 3em;
  position: relative;
  overflow: hidden;
  z-index: 1;
  transition: 0.5s;
  margin: 2rem;
  cursor: pointer;
  padding: 0.5rem 4rem;
  background-color: rgb(255,255,255,.1);
  height: fit-content;
  white-space: nowrap;
}
@media screen and (max-width: 550px) {
	.BubbleButton {
      padding: 0.5rem 1rem;

   }
}
.BubbleButton span {
  position: absolute;
  width: 25%;
  height: 150%;
  background-color: var(--c);
  transform: translateY(150%);
  border-radius: 50%;
  left: calc((var(--n) - 1) * 25%);
  transition: 0.5s;
  transition-delay: calc((var(--n) - 1) * 0.12s);
  z-index: -1;
}

.BubbleButton:hover {
  color: var(--Black);
}

.BubbleButton:hover span {
  transform: translateY(0) scale(2);
}

.BubbleButton span:nth-child(1) {
  --n: 1;
}

.BubbleButton span:nth-child(2) {
  --n: 2;
}

.BubbleButton span:nth-child(3) {
  --n: 3;
}

.BubbleButton span:nth-child(4) {
  --n: 4;
}
